'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { appointmentsAPI, patientsAPI, Patient } from '@/lib/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Input } from '@/components/ui/Input';
import { Button } from '@/components/ui/Button';

export default function NewAppointmentPage() {
  const router = useRouter();
  const [patientId, setPatientId] = useState('');
  const [startAt, setStartAt] = useState('');
  const [endAt, setEndAt] = useState('');
  const [status, setStatus] = useState<'scheduled'|'completed'|'cancelled'|'no-show'>('scheduled');
  const [notes, setNotes] = useState('');
  const [patients, setPatients] = useState<Patient[]>([]);
  const [error, setError] = useState('');

  useEffect(() => {
    let cancelled = false;
    const loadPatients = async () => {
      const res = await patientsAPI.list();
      if (!cancelled) {
        setPatients(res.data);
      }
    };
    loadPatients();
    return () => { cancelled = true; };
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    try {
      await appointmentsAPI.create({ patientId, startAt, endAt: endAt || undefined, status, notes });
      router.push('/dashboard/appointments');
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to create appointment');
    }
  };

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-3xl font-bold text-gray-900">New Appointment</h2>
        <p className="text-gray-600 mt-1">Schedule a new patient appointment</p>
      </div>
      <Card className="shadow-lg border-gray-200">
        <CardHeader className="bg-gradient-to-r from-green-50 to-emerald-50 border-b">
          <CardTitle className="text-xl">Appointment Details</CardTitle>
        </CardHeader>
        <CardContent className="pt-6">
          <form onSubmit={handleSubmit} className="space-y-6 max-w-2xl">
            <div>
              <label className="block text-sm font-medium text-gray-900 mb-2">Patient</label>
              <select value={patientId} onChange={(e) => setPatientId(e.target.value)} className="w-full rounded-lg border-2 border-gray-300 px-4 py-3 text-gray-900 focus:border-green-500 focus:ring-2 focus:ring-green-200 transition-all" required>
                <option value="">Select a patient</option>
                {patients.map(p => (
                  <option key={p._id} value={p._id}>{p.name}</option>
                ))}
              </select>
            </div>
            <Input label="Start Date & Time" type="datetime-local" value={startAt} onChange={(e) => setStartAt(e.target.value)} required />
            <Input label="End Date & Time (optional)" type="datetime-local" value={endAt} onChange={(e) => setEndAt(e.target.value)} />
            <div>
              <label className="block text-sm font-medium text-gray-900 mb-2">Status</label>
              <select value={status} onChange={(e) => setStatus(e.target.value as any)} className="w-full rounded-lg border-2 border-gray-300 px-4 py-3 text-gray-900 focus:border-green-500 focus:ring-2 focus:ring-green-200 transition-all">
                <option value="scheduled">Scheduled</option>
                <option value="completed">Completed</option>
                <option value="cancelled">Cancelled</option>
                <option value="no-show">No-show</option>
              </select>
            </div>
            <Input label="Notes" value={notes} onChange={(e) => setNotes(e.target.value)} placeholder="Add any additional notes..." />
            {error && <div className="bg-red-50 text-red-700 px-4 py-3 rounded-lg text-sm font-medium">{error}</div>}
            <div className="flex gap-3 pt-4">
              <Button type="submit" className="flex-1 py-3 bg-gradient-to-r from-green-600 to-emerald-600 hover:from-green-700 hover:to-emerald-700 shadow-lg font-semibold">
                <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                </svg>
                Create Appointment
              </Button>
              <Button type="button" variant="outline" onClick={() => router.push('/dashboard/appointments')} className="px-6">
                Cancel
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}
